@charset "UTF-8";
.game {
  float: left; }
  .game > * {
    margin: 5px; }
  .game__board {
    float: left; }
  .game__info {
    float: left; }
  .game__list {
    float: left;
    clear: both; }
  .game .game__info {
    border: 1px solid #666;
    padding: 10px; }
    .game .game__info .button {
      color: #999;
      background: #444;
      margin-top: 5px;
      margin-right: 5px;
      border: 2px solid #666;
      padding: 5px; }
      .game .game__info .button-nuke {
        color: #e00; }
    .game .game__info .actions {
      padding: 0 0 10px 0; }
    .game .game__info .interaction-container {
      display: grid;
      font-family: Verdana;
      border: 1px solid #555;
      grid-template-columns: 50% 50%;
      margin-bottom: 10px;
      padding: 10px; }
      .game .game__info .interaction-container .selected,
      .game .game__info .interaction-container .targeted {
        display: block;
        padding: 10px;
        border: 1px solid #746200; }
        .game .game__info .interaction-container .selected__label,
        .game .game__info .interaction-container .targeted__label {
          display: block;
          text-align: center;
          padding: 5px; }
      .game .game__info .interaction-container .selected {
        border-color: #095c09;
        margin-right: 10px; }
        .game .game__info .interaction-container .selected__label {
          background: #095c09;
          border: 1px solid #13af13; }
      .game .game__info .interaction-container .targeted {
        border-color: #ac7000; }
        .game .game__info .interaction-container .targeted__label {
          background: #ac7000;
          border: 1px solid #d88c00;
          color: black; }
      .game .game__info .interaction-container .targeted ul {
        list-style: none;
        padding: 0;
        margin: 0;
        margin-top: 3px; }

.step-counter {
  background: black;
  position: relative;
  top: 1px;
  margin-right: 5px;
  padding: 5px;
  border: 2px solid black; }

.auto-cycle {
  white-space: pre;
  top: 1px;
  position: relative;
  color: #222; }
  .auto-cycle span {
    padding-left: 15px; }
  .auto-cycle input {
    display: none;
    border: 1px solid #666;
    background: #222; }
    .auto-cycle input:checked ~ span {
      color: white;
      position: relative; }
      .auto-cycle input:checked ~ span::before {
        position: absolute;
        left: 1px;
        top: -1px;
        content: "âœ“"; }
